var tha;
import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			wxUserInfo: {},
			urecommend: '1',
			name:'',
			phone: '',
			password: '',
			verification: ''
		};
	},
	onLoad:function(options) {
		console.log(options.urecommend);
		this.urecommend = options.u_id
	},
	
	methods: {
		/**
		 * 账号密码登录
		 */
		login(e) {
			var me = this;
			if (me.name == '') {
				uni.showToast({
					title: '姓名不能为空',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			if (me.phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			// if (me.password == '') {
			// 	uni.showToast({
			// 		title: '验证码不能为空',
			// 		icon: 'none',
			// 		duration: 1500
			// 	});
			// 	return false;
			// }
			// if (me.password != this.verification) {
			// 	uni.showToast({
			// 		title: '验证码错误',
			// 		icon: 'none',
			// 		duration: 1500
			// 	});
			// 	return false;
			// }
			localStorage.setItem("name",me.name);
			localStorage.setItem("phone",me.phone);
			localStorage.setItem("urecommend",me.urecommend);
			
			uni.switchTab({
				url: "/pages/my/my"
			});
		},
		/**			  获取验证码方法			 */ 
		yanzhengma(e) {
			var me = this;
			if (me.phone == '') {
				uni.showToast({
					title: '手机号不能为空',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			if (me.phone.length != 11) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 1500
				});
				return false;
			}
			uni.showToast({
				title: '发送成功,请注意查收',
				icon: 'none',
				duration: 1500
			});
			uni.request({
				url: 'http://localhost:8080/user/duanxin?mobileString=' + this.phone,
				method: 'POST',
				success: res => {
					
					this.verification = res.data;
				}
			});
		},
	},
}
